function canInput(element) {
  return element.tagName === 'INPUT' && ['email', 'number', 'password', 'search', 'tel', 'text', 'url'].indexOf(element.type) > -1;
}

function getBoolOrLowerCase(val) {
  return typeof val === 'boolean' ? val : val.toString().toLowerCase();
}

function getUrlParams() {
  let params = [`page=${this.page}`, `display=${this.display}`];

  if (this.order !== null) {
    params.push('order=' + encodeURIComponent(`${this.order.rel} ${this.order.asc ? 'asc' : 'desc'}`));
  }

  if (this.filters.size > 0) {
    this.filters.forEach((val, key) => {
      if (Array.isArray(val)) {
        params.push(`filters[][${encodeURIComponent(key)}][]=` + val.map(encodeURIComponent).join(`&filters[][${encodeURIComponent(key)}][]=`));
      } else {
        params.push(`filters[][${encodeURIComponent(key)}]` + (val === true ? '' : `=${encodeURIComponent(val)}`));
      }
    });
  }

  return params.join('&');
};

function setIndex(current, index) {
  current['$index'] = this.start + index + 1;

  return current;
}

function sort(a, b) {
  let rel = this.rel;

  if (rel in a && rel in b) {
    let first = typeof a[rel] === 'number' ? a[rel] : a[rel].toString().toLowerCase();
    let second = typeof b[rel] === 'number' ? b[rel] : b[rel].toString().toLowerCase();

    if (first < second) {
      return this.asc ? -1 : 1;
    } else if (first > second) {
      return this.asc ? 1 : -1;
    }
  }

  return 0;
}

export {canInput, getBoolOrLowerCase, getUrlParams, setIndex, sort};



